import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import NavBar from"./components/NavBar.vue"
import ParkingNavbar from"./components/ParkingNavbar.vue"
import ParkingPage from './components/ParkingPage.vue';
import HomePage from './components/HomePage.vue'
import FooterPage from"./components/FooterPage.vue"
import AboutPage from"./components/AboutPage.vue"
import demoPage from "./components/demoPage.vue"
import InoxPage from "./components/InoxPage.vue"
import ContactPage from"./components/ContactPage.vue"
import MallPage from"./components/MallPage.vue"
import MarketPage from "./components/MarketPage.vue"
import StationPage from "./components/StationPage.vue"
import SignupPage from "./components/SignupPage.vue"
import ReviewPage from"./components/ReviewPage.vue"
import LiveReview from "./components/LiveReview.vue"
Vue.use(VueRouter);

const routes = [
  {
    path: '/demo',
    name: 'demPage',
    component: demoPage
  },
  {
    path: '/livereview',
    name: 'LiveReview',
    component:LiveReview
  },
  {
    path: '/review',
    name: 'ReviewPage',
    component:ReviewPage
  },
  {
    path: '/',
    name: 'SignupPage',
    component: SignupPage
  },
  {
    path: '/contact',
    name: 'ContactPage',
    component:ContactPage
  },
  {
    path: '/ccm',
    name: 'MallPage',
    component: MallPage
  },
  {
    path: '/railway',
    name: 'StationPage',
    component: StationPage
  },
  {
    path: '/market',
    name: 'MarketPage',
    component: MarketPage
  },
  {
    path: '/inox',
    name: 'InoxPage',
    component: InoxPage
  },
  {
    path: '/navbar',
    name: 'NavBar',
    component: NavBar
  },
  {
    path: '/pnavbar',
    name: 'ParkingNavbar',
    component: ParkingNavbar
  },
  {
    path: '/home',
    name: 'HomePage',
    component:HomePage
  },
  {
    path: '/about',
    name: 'AboutPage',
    component:AboutPage
  },
  {
    path: '/parking',
    name: 'ParkingPage',
    component: ParkingPage
  },
  {
    path: '/footer',
    name: 'FooterPage',
    component: FooterPage
  },

] 

const router = new VueRouter({  mode:'history', routes, linkExactActiveClass:'active' })




Vue.config.productionTip = false

new Vue({
  router:router,
  render: h => h(App),
}).$mount('#app')

