<template>
    <div>
        <section id="contact">
            <div class="sectionheader">
                <h1>CONTACT</h1>
            </div>
            <article>
                <p>We'd love to hear from you! If you have any questions, comments, or suggestions, please don't hesitate to
                    get in touch with us. </p>

                <ul style="color: #0dcaf0;">
                    <li>
                        Phone:7666943359
                    </li>
                    <li>
                        Email:carlelo264@gmail.com
                    </li>
                </ul>

                <p> Thank you for your interest in our company. We value your feedback and look forward to hearing from you!
                </p>

                <label for="checkcontact" class="contactbutton">
                    <div class="mail"></div>
                </label><input id="checkcontact" type="checkbox">

                <form class="contactform" @submit.prevent="submitContact" method="post">
                    <p class="input_wrapper"><input type="text" name="contact_nom" value="" id="contact_nom"
                            v-model="names"><label for="contact_nom">NAME</label></p>
                    <p class="input_wrapper"><input type="text" name="contact_email" value="" id="contact_email"
                            v-model="email"><label for="contact_email">EMAIL</label></p>
                    <p class="input_wrapper"><input type="text" name="contact_sujet" value="" id="contact_sujet"
                            v-model="subject"><label for="contact_sujet">SUBJECT</label></p>
                    <p class="textarea_wrapper"><textarea name="contact_message" id="contact_message"
                            v-model="msg"></textarea></p>
                    <p class="submit_wrapper"><input type="submit" value="SEND"></p>
                </form>
            </article>
        </section>
    </div>
</template>

<script>
import axios from 'axios';
export default {

    data() {
        return {
            names: "",
            email: "",
            subject: "",
            msg: ""
        };
    },
    methods: {
        async submitContact() {
            try {
                const response = await axios.post('http://127.0.0.1:5000/contacts', {

                    email: this.email,
                    names: this.names,
                    subject: this.subject,
                    msg: this.msg
                });

                console.log(response.data);
                alert('Your message has been successfully saved');
            } 
            catch (error) {
                console.error(error);

                if (error.response) {
                    console.log(error.response.data);
                    console.log(error.response.status);
                    console.log(error.response.headers);
                } else if (error.request) {
                    console.log(error.request);
                } else {
                    console.log('Error', error.message);
                }

                alert('An error occurred while trying to save your message. Please try again later.');
            }
        },
    }

};
</script>

