<template>
    <div>
    <br>      <div class="container">
        <div style="max-height: 50px; background-color: #0DCAF0; color: white">
          
        </div>
  
        <section class="row" style="margin-top: 20px">
          <div v-for="review in reviews" :key="review._id" class="card col-md-6">
            <div class="card-body">
              <blockquote class="blockquote blockquote-custom bg-white px-3 pt-4">
                <div class="blockquote-custom-icon bg-primary shadow-1-strong" >
                  <i class="fa fa-quote-left text-white"></i>
                </div>
               
                <p class="mb-0 mt-2 font-italic">
                  <b> Review:</b> {{ review.review }}
                  <a class="text-info"
                    ><router-link to="/contact">@CarleloPark</router-link></a
                  >."
                </p>
                <footer class="blockquote-footer pt-4 mt-4 border-top">
                  <b> Rating: </b>
                  <cite title="Source Title">{{ review.rating }}</cite>
                </footer>
                <footer class="blockquote-footer pt-4 mt-4 border-top">
                  <b>Author</b>
                  <cite title="Source Title">{{ review.name }}</cite>
                </footer>
              </blockquote>
            </div>
          </div>
        </section>
      </div>
    </div>
  </template>
    
    <script>
  import axios from "axios";
  
  export default {
    data() {
      return {
        reviews: [],
      };
    },
    async created() {
      try {
        const response = await axios.get('http://127.0.0.1:5000/review');
        this.reviews = response.data;
      } catch (error) {
        console.error(error);
        alert(error);
      }
    },
  };
  </script>
    
<style scoped>
 
  
  .blockquote-custom {
    position: relative;
    font-size: 1.1rem;
  }
  
  .blockquote-custom-icon {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: -40px;
    left: 19px;
  }
  </style>